@charset "UTF-8";
@font-face {
  font-family: "PT Mono";
  src: url("../fonts/PTMono-Regular.woff2") format("woff2"), url("../fonts/PTMono-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Medium.woff2") format("woff2"), url("../fonts/Roboto-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/RobotoCondenced-Regular.woff2") format("woff2"), url("../fonts/RobotoCondenced-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

html {
  box-sizing: border-box; }

body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 22px;
  font-family: "PT Mono", "Courier New", monospace;
  font-weight: normal;
  font-style: normal; }

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  overflow: hidden; }

.button {
  padding: 9px 23px 9px 24px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: none;
  color: #FFFFFF;
  border: 1px solid #FFFFFF;
  border-radius: 5px;
  cursor: pointer; }
  .button:hover {
    color: #00B2FF;
    background-color: #FFFFFF; }

.page-header__logo {
  display: flex;
  flex-grow: 1;
  text-decoration: none;
  cursor: pointer; }

.logo__img {
  margin-right: 16px; }

.logo__text {
  margin-top: 8px; }

.logo__text-name {
  color: #FFFFFF;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 25px;
  font-weight: 700; }
  @media (max-width: 320px) {
    .logo__text-name {
      display: none; } }

.logo__text-description {
  color: #788891;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 12px;
  font-weight: 500; }
  @media (max-width: 320px) {
    .logo__text-description {
      display: none; } }

.page-header {
  background-color: #1b2d37;
  display: flex; }

.page-header__wrapper {
  width: 1512px;
  margin: 33px auto 0;
  padding: 0 50px;
  display: flex;
  justify-items: space-between;
  color: #FFFFFF; }
  @media (max-width: 1023px) {
    .page-header__wrapper {
      margin-top: 20px;
      padding: 0 23px 20px 25px; } }
  @media (max-width: 320px) {
    .page-header__wrapper {
      padding: 0 17px 0 17px;
      margin-top: 17px; } }

.page-header__nav {
  display: flex;
  align-items: center;
  margin-right: 2px; }
  @media (max-width: 1023px) {
    .page-header__nav {
      width: 85px;
      height: 20px; } }

.page-header__mail {
  position: relative; }
  .page-header__mail::before {
    position: absolute;
    content: "";
    background-image: url("../img/envelope.svg");
    background-repeat: no-repeat;
    background-size: contain;
    width: 19px;
    height: 14px;
    left: 0;
    top: 4px; }
  @media (max-width: 1023px) {
    .page-header__mail::before {
      width: 25px;
      height: 19px; } }

.page-header__call {
  position: relative; }
  .page-header__call::before {
    position: absolute;
    content: "";
    background-image: url("../img/phone_call.svg");
    background-repeat: no-repeat;
    background-size: contain;
    width: 18px;
    height: 18px;
    left: 0;
    top: 2px; }
  @media (max-width: 1023px) {
    .page-header__call::before {
      width: 24px;
      height: 24px; } }

.page-header__mail a,
.page-header__call a {
  font-family: "PT Mono", "Courier New", monospace;
  font-size: 16px;
  font-weight: 400;
  text-decoration: none;
  color: #FFFFFF; }
  @media (max-width: 1023px) {
    .page-header__mail a,
    .page-header__call a {
      display: none; } }

.page-header__call {
  margin-left: 60px; }

.page-header__callback {
  max-width: 185px;
  margin-left: 50px; }
  @media (max-width: 1023px) {
    .page-header__callback {
      display: none; } }

.page-main {
  scroll-behavior: smooth;
  min-height: 800px; }

.page-main__wrapper {
  margin: -90px 0 0;
  background-color: #1b2d37;
  background-image: url(../img/bg-pattern.png); }

.page-main__main {
  max-width: 1623px;
  height: 800px;
  margin: 0 auto;
  padding: 0 0 0 100px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start; }
  .page-main__main::before {
    position: absolute;
    content: "";
    width: 600px;
    height: 500px;
    background-image: url(../img/bg-polygons.svg);
    background-size: contain;
    background-repeat: no-repeat;
    top: 140px;
    right: 50px;
    transform: scale(1.5); }
  @media (max-width: 1023px) {
    .page-main__main::before {
      width: 400px;
      height: 400px;
      top: 50px;
      right: 50px; } }
  @media (max-width: 767px) {
    .page-main__main {
      padding: 20px; }
      .page-main__main::before {
        width: 473px;
        height: 300px;
        top: 100px;
        right: -200px; } }
  @media (max-width: 320px) {
    .page-main__main::before {
      width: 420px;
      height: 215px;
      top: 100px;
      right: -341px; } }

.page-main__text {
  padding-top: 200px;
  margin-bottom: 20px;
  z-index: 5;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 80px;
  font-weight: 700;
  text-transform: uppercase;
  line-height: 94px;
  color: #FFFFFF; }
  @media (max-width: 1023px) {
    .page-main__text {
      width: 470px;
      font-size: 60px;
      line-height: 70px; } }
  @media (max-width: 320px) {
    .page-main__text {
      width: auto;
      padding-top: 300px;
      line-height: 47px;
      font-size: 40px; } }

.page-main__title {
  margin: 0;
  max-width: 683px;
  margin: 0 0 44px;
  padding-left: 98px;
  position: relative;
  z-index: 5;
  font-family: "PT Mono", "Courier New", monospace;
  font-weight: 400;
  font-size: 22px;
  line-height: 38px;
  color: #FFFFFF; }
  .page-main__title::before {
    position: absolute;
    content: "";
    width: 66px;
    height: 2px;
    background-color: #fff;
    left: 2px;
    top: 16px; }
  @media (max-width: 1023px) {
    .page-main__title {
      padding-left: 48px;
      width: 356px;
      font-size: 18px;
      line-height: 28px; }
      .page-main__title::before {
        width: 33px; } }
  @media (max-width: 320px) {
    .page-main__title {
      width: 245px;
      font-size: 16px; } }

.page-main__consult {
  width: 407px;
  min-height: 52px;
  margin: 0 0 131px 98px;
  padding: 16px 45px 19px 44px;
  position: relative;
  box-sizing: border-box;
  z-index: 5;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: .05em;
  text-transform: uppercase;
  text-decoration: none;
  color: #FFFFFF;
  background-color: #00B2FF;
  border-radius: 5px; }
  .page-main__consult:hover {
    color: #00B2FF;
    background-color: #FFFFFF; }
  .page-main__consult::before {
    position: absolute;
    width: auto;
    height: 24px;
    content: "Получить бесплатную консультацию"; }
  @media (max-width: 1023px) {
    .page-main__consult {
      margin-left: 50px; } }
  @media (max-width: 767px) {
    .page-main__consult {
      width: 287px;
      margin: 0 auto; }
      .page-main__consult::before {
        left: 33px;
        content: "Бесплатная консультация"; } }

.page-main__scroll-down {
  margin: 0 0 127px -35px;
  color: #FFFFFF;
  opacity: 0.5;
  transform: rotate(-90deg);
  cursor: pointer; }
  @media (max-width: 1023px) {
    .page-main__scroll-down {
      display: none; } }

.page-main__product-img {
  position: absolute;
  width: 705px;
  height: 515px;
  top: 170px;
  right: 30px; }
  @media (max-width: 1023px) {
    .page-main__product-img {
      width: 465px;
      height: 340px;
      top: 127px;
      right: 0; } }
  @media (max-width: 767px) {
    .page-main__product-img {
      width: 465px;
      height: 340px; } }
  @media (max-width: 320px) {
    .page-main__product-img {
      width: 301px;
      height: 220px;
      top: 131px;
      right: 0; } }
