@font-face {
    font-family: "PT Mono";
    src: 
      url("../fonts/PTMono-Regular.woff2") format("woff2"),
      url("../fonts/PTMono-Regular.woff") format("woff");
    font-weight: 400;
    font-style: normal;
    font-display: swap;
  }

  @font-face {
    font-family: "Roboto";
    src: 
      url("../fonts/Roboto-Medium.woff2") format("woff2"),
      url("../fonts/Roboto-Medium.woff") format("woff");
    font-weight: 500;
    font-style: normal;
    font-display: swap;
  }

  @font-face {
    font-family: "Roboto Condensed";
    src: 
      url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"),
      url("../fonts/RobotoCondensed-Bold.woff") format("woff");
    font-weight: 700;
    font-style: normal;
    font-display: swap;
  }

  @font-face {
    font-family: "Roboto Condensed";
    src:
      url("../fonts/RobotoCondenced-Regular.woff2") format("woff2"),
      url("../fonts/RobotoCondenced-Regular.woff") format("woff");
    font-weight: 400;
    font-style: normal;
    font-display: swap;
  }